___CSS_LOADER_EXPORT___.push([module.id, ".fade:not(.show){opacity:1}", "",{"version":3,"sources":["webpack://./resources/js/components/buses/DeleteBus.vue"],"names":[],"mappings":"AAyEA,iBACA,SACA","sourcesContent":["<template>\r\n  <div class=\"modal\" id=\"delete-bus\">\r\n    <div class=\"modal-dialog\">\r\n      <div class=\"modal-content\">\r\n        <!-- Modal Header -->\r\n        <div class=\"modal-header\">\r\n          <h4 class=\"modal-title\">Delete Bus</h4>\r\n          <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n            &times;\r\n          </button>\r\n        </div>\r\n\r\n        <!-- Modal body -->\r\n        <div class=\"modal-body\">\r\n          <div class=\"mb-2\">\r\n            <p>\r\n              Are you sure you want to delete <b>{{ `${bus.bus_name} ${bus.bus_reg_no}`  }}</b>?\r\n            </p>\r\n          </div>\r\n\r\n          <div class=\"col-12 mt-3 text-right\">\r\n            <button class=\"btn btn-primary\" data-dismiss=\"modal\">Cancel</button>\r\n            <button class=\"btn btn-danger\" @click=\"deleteBus\">\r\n              Delete\r\n            </button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  emits: [\"update\"],\r\n  props: {\r\n    bus: {\r\n      type: Object,\r\n      required: true,\r\n    },\r\n  },\r\n  data() {\r\n    return {};\r\n  },\r\n  methods: {\r\n    deleteBus() {\r\n      this.$store.dispatch(\"startLoading\");\r\n      axios\r\n        .post(`/admin/buses/${this.bus.id}`, {\r\n          _method: \"DELETE\",\r\n        })\r\n        .then((res) => {\r\n          this.closeModal();\r\n\r\n          this.$nextTick(() => {\r\n            this.$emit(\"update\");\r\n          });\r\n        })\r\n        .catch((res) => {\r\n          this.closeModal();\r\n          this.$store.dispatch(\"stopLoading\");\r\n        });\r\n    },\r\n    closeModal() {\r\n      $(\"#delete-bus\").modal(\"hide\");\r\n    },\r\n  },\r\n  created() {\r\n    const self = this;\r\n  },\r\n};\r\n</script>\r\n<style>\r\n.fade:not(.show) {\r\n  opacity: 1;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
